.map__inner {
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  left: 0;
}

// ruimte voor sluitknop
.bvmd-popup__header {
  max-width: calc(100% - 30px);
}

// kop
.bvmd-popup__header-links {
  margin: 0;
  font-weight: bold;
  font-variant: small-caps;
}

.bvmd-popup__brood {
}
.bvmd-popup__tekst {
  margin: 0;
}
.bvmd-popup__adres {
  margin-bottom: 0;
  line-height: inherit;
}
.bvmd-popup__adres-lijst {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.map__aside {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 500;
  padding: 20px;
  background-color: #f5f5f5;
  max-width: 300px;
}

.map__aside .map__heading:first-of-type {
  margin-top: 0;
}
.map__section:last-child {
  p,
  ul,
  ol {
    &:last-child {
      margin-bottom: 0;
    }
  }
}

// leaflet overschrijvingen
.leaflet-marker-icon[alt~="color-red"] {
  filter: hue-rotate(140deg);
}
.leaflet-marker-icon[alt~="color-purple"] {
  filter: hue-rotate(65deg);
}
.leaflet-popup-content-wrapper,
.leaflet-popup-tip {
  background-color: #f5f5f5;
  color: #333;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.4);
}

.leaflet-popup-content-wrapper {
  border-radius: 0;
}

.leaflet-popup-content {
  margin: 0;
  margin: 15px;
  min-width: 200px;
  line-height: inherit;
}

.leaflet-container a.leaflet-popup-close-button {
  width: 26px;
  height: 26px;
  font: 26px/26px Tahoma, Verdana, sans-serif;
}
